




































// mescroll-uni.js,
import MeScroll from './mescroll-uni.js';
// 
import GlobalOption from './mescroll-uni-option.js';

export default {
	data() {
		return {
			mescroll: null,
			downHight: 0, //: 
			downRotate: 0, //: 
			downText: '', //: 
			isDownReset: false, //: 
			isDownLoading: false, //: 
			isUpLoading: false, // :  "..."
			isUpNoMore: false, // :  "-- END --"
			isShowEmpty: false, // 
			isShowToTop: false // 
		}
	},
	props: {
		down: Object, // 
		up: Object, // 
		top: [String,Number],  // padding-top,upx. 
		bottom: [String,Number]  // padding-bottom,upx. 
	},
	computed: {
		// 
		optDown() {
			return this.mescroll ? this.mescroll.optDown : null;
		},
		// 
		optUp() {
			return this.mescroll ? this.mescroll.optUp : null;
		},
		// 
		optEmpty() {
			return this.mescroll ? this.mescroll.optUp.empty : null;
		},
		// 
		optToTop() {
			return this.mescroll ? this.mescroll.optUp.toTop : null;
		},
		// padding-top,upx,px. 
		padTop(){
			return uni.upx2px(Number(this.top) || 0)+'px';
		},
		// padding-bottom,upx,px 
		padBottom(){
			return uni.upx2px(Number(this.bottom) || 0)+'px';
		}
	},
	methods: {
		//touchstart,
		touchstartEvent(e) {
			this.mescroll && this.mescroll.touchstartEvent(e);
		},
		//touchmove,
		touchmoveEvent(e) {
			this.mescroll && this.mescroll.touchmoveEvent(e);
		},
		//touchend,
		touchendEvent(e) {
			this.mescroll && this.mescroll.touchendEvent(e);
		},
		// 
		emptyClick(){
			this.$emit('emptyclick',this.mescroll)
		},
		// 
		toTopClick(){
			this.isShowToTop = false; // ,,
			uni.pageScrollTo({ // 
				scrollTop: 0,
				duration: this.mescroll.optUp.toTop.duration
			});
			this.$emit('topclick',this.mescroll) // 
		}
	},
	// H5,onReady,mounted
	mounted() {
		let vm = this;
		
		let diyOption = {
			// 
			down: {
				inOffset(mescroll) {
					// offset
					vm.isDownReset = false; //  (mescroll,)
					vm.isDownLoading = false; // 
					vm.downText = mescroll.optDown.textInOffset; // 
				},
				outOffset(mescroll) {
					// offset
					vm.isDownReset = false; //  (mescroll,)
					vm.isDownLoading = false; // 
					vm.downText = mescroll.optDown.textOutOffset; // 
				},
				onMoving(mescroll, rate, downHight) {
					// ,; rate(inOffset: rate<1; outOffset: rate>=1); downHight
					vm.downHight = downHight; //  (mescroll,)
					vm.downRotate = 360 * rate; // 
				},
				showLoading(mescroll, downHight) {
					// 
					vm.isDownReset = true; //  (mescroll,)
					vm.isDownLoading = true;// 
					vm.downHight = downHight; //  (mescroll,)
					vm.downText = mescroll.optDown.textLoading; // 
				},
				endDownScroll(mescroll) {
					vm.isDownReset = true;//  (mescroll,)
					vm.isDownLoading = false; // 
					vm.downHight = 0; //  (mescroll,)
				},
				// 
				callback: function(mescroll){
					vm.$emit('down', mescroll)
				}
			},
			// 
			up: {
				// 
				showLoading() {
					vm.isUpLoading = true;
					vm.isUpNoMore = false;
				},
				// 
				showNoMore() {
					vm.isUpLoading = false;
					vm.isUpNoMore = true;
				},
				// 
				hideUpScroll() {
					vm.isUpLoading = false;
					vm.isUpNoMore = false;
				},
				// 
				empty: {
					onShow(isShow) { // 
						if(vm.isShowEmpty != isShow) vm.isShowEmpty = isShow;
					}
				},
				// 
				toTop: {
					onShow(isShow) { // 
						if (vm.isShowToTop != isShow) vm.isShowToTop = isShow;
					}
				},
				// 
				callback: function(mescroll){
					vm.$emit('up', mescroll)
				}
			}
		}
		
		MeScroll.extend(diyOption, GlobalOption); // 
		let myOption = MeScroll.extend({
			'down': vm.down ? JSON.parse(JSON.stringify(vm.down)) : vm.down, // ,props
			'up': vm.up ? JSON.parse(JSON.stringify(vm.up)) : vm.up // ,props
		}, diyOption); // 
		
		// MeScroll
		vm.mescroll = new MeScroll(myOption);
		// initmescroll
		vm.$emit('init', vm.mescroll);

		// mescrollbody,downbottomOffset
		uni.getSystemInfo({
			success(res) {
				vm.mescroll.setBodyHeight(res.windowHeight);
			}
		});
	}
}
